<template>
  <div class="container">
    <!-- 角色 -->
    <figure class="birds" :style="`left:${redBird.left}vw;bottom:${redBird.bottom}vh${redBirdReverse}`">
      <img v-show="redBird.urlSwitch" src="../assets/redBird1.png" />
      <img v-show="!redBird.urlSwitch" src="../assets/redBird2.png" />
    </figure>
    <figure class="birds" :style="`left:${hato.left}vw;bottom:${hato.bottom}vh${hatoReverse}`">
      <img v-show="hato.urlSwitch" src="../assets/hato1.png" />
      <img v-show="!hato.urlSwitch" src="../assets/hato2.png" />
    </figure>
    <!-- 圈數比分 -->
    <div class="score">{{ hato.round }}：{{ redBird.round }}</div>
    <!-- 提示文字 -->
    <h2 class="hint">{{ titleMsg }}</h2>
    <h2 class="mainMsg" v-html="mainMsg"></h2>
    <!-- 骰子 -->
    <figure class="dice" @click.stop="turnStart">
      <img v-show="diceNum == 1" src="../assets/dice1.png" />
      <img v-show="diceNum == 2" src="../assets/dice2.png" />
      <img v-show="diceNum == 3" src="../assets/dice3.png" />
      <img v-show="diceNum == 4" src="../assets/dice4.png" />
      <img v-show="diceNum == 5" src="../assets/dice5.png" />
      <img v-show="diceNum == 6" src="../assets/dice6.png" />
    </figure>
    <!-- 行動力 -->
    <div class="ui">
      <figure>
        <img src="../assets/boot.png" alt="" />
      </figure>
      <p>× {{ hato.actions }}</p>
    </div>
    <!-- 賽道 -->
    <!-- 第n層雲格 -->
    <!-- 共5層，25朵雲 -->
    <div class="first">
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
    </div>
    <div class="second">
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure class="floatRight"><img src="../assets/cloud.png" /></figure>
    </div>
    <div class="third">
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure class="floatRight"><img src="../assets/cloud.png" /></figure>
      <figure class="floatRight"><img src="../assets/cloud.png" /></figure>
    </div>
    <div class="fourth">
      <figure><img src="../assets/cloud.png" /></figure>
      <figure class="floatRight"><img src="../assets/cloud.png" /></figure>
      <figure class="floatRight"><img src="../assets/cloud.png" /></figure>
    </div>
    <div class="fifth">
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
      <figure><img src="../assets/cloud.png" /></figure>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      diceNum: 1,
      superDice: false,
      shitDice: false,
      allowDice: true, // false時禁止擲骰子，避免用戶於回合結束前多次點擊骰子
      titleMsg: 'your turn',
      mainMsg: '',
      hato: {
        name: '小鴿子',
        urlSwitch: true, // 輪播圖控制切換角色圖片
        position: 0, // 目前所在格數
        left: 4,
        bottom: 8,
        round: 0,
        actions: 3
      },
      redBird: {
        name: '大紅鳥',
        urlSwitch: true,
        position: 0,
        left: 3,
        bottom: 8,
        round: 0
      }
    }
  },
  methods: {
    init() {
      this.diceNum = 1
      this.superDice = false
      this.shitDice = false
      this.allowDice = true
      this.titleMsg = 'your turn'
      this.mainMsg = ''
      this.hato.position = 0
      this.hato.left = 4
      this.hato.bottom = 8
      this.hato.round = 0
      this.hato.actions = 3
      this.redBird.position = 0
      this.redBird.left = 3
      this.redBird.bottom = 8
      this.redBird.round = 0
    },
    async turnStart() {
      if (this.allowDice) {
        this.allowDice = false // 回合進行中禁止多次投骰
        // 判斷是否尚有行動力
        if (this.hato.actions <= 0) {
          this.mainMsg = '小鴿子累了，<br>本回合無法行動。'
          await new Promise((resolve) => {
            window.addEventListener('click', () => {
              resolve()
            })
          })
          this.hato.actions += 1
          this.mainMsg = '經過休息，<br>恢復了一點行動值。'
          await new Promise((resolve) => {
            window.addEventListener('click', () => {
              resolve()
            })
          })
          this.mainMsg = ''
        } else {
          this.hato.actions -= 1
          this.diceChange()
          // 等待投骰動畫
          await new Promise((resolve) => {
            setTimeout(() => resolve(), 1500)
          })
          // 判斷有無幸運草或鳥屎
          if (this.superDice) {
            this.superDice = false
            this.diceNum = 6
          }
          if (this.shitDice) {
            this.shitDice = false
            this.diceNum = 1
          }
          this.titleMsg = 'progressing'
          // 依照骰出數值移動i次
          for (let i = this.diceNum; i > 0; i--) {
            this.move(this.hato)
          }
          await new Promise((resolve) => {
            setTimeout(() => resolve(), 1500)
          })
          await this.event()
        }

        // 敵人回合
        this.diceChange()
        this.titleMsg = "enemy's turn"
        await new Promise((resolve) => {
          setTimeout(() => resolve(), 1500)
        })
        this.titleMsg = 'progressing'
        for (let i = this.diceNum; i > 0; i--) {
          this.move(this.redBird)
        }
        await new Promise((resolve) => {
          setTimeout(() => resolve(), 1500)
        })
        // 待回合全結束
        this.titleMsg = 'your turn'
        this.allowDice = true
      }
    },
    diceChange() {
      let count = 0 //展示骰子數值變動count次，達成投骰動畫
      let interval = setInterval(() => {
        this.diceNum = Math.floor(Math.random() * 6 + 1)
        count++
        if (count > 10) clearInterval(interval)
      }, 100)
    },
    async move(who) {
      // 根據人物當前所在格子決定前進方向
      if (who.position <= 6 || who.position == 8 || who.position == 10) who.left += 9
      if (who.position == 7 || who.position == 9 || who.position == 11 || who.position == 12) who.bottom += 15
      if ((who.position >= 13 && who.position <= 17) || who.position == 19 || who.position == 20 || who.position == 22 || who.position == 23) who.left -= 9
      if (who.position == 18 || who.position == 21 || who.position == 24) who.bottom -= 15
      if (who.position == 25) {
        who.bottom -= 15
        who.position = -1 // 人物前進一格後回到原點，position歸0
        who.round += 1 //跑完一圈
        if (who.round == 3) {
          this.mainMsg = `勝負分曉！<br>勝者：${who.name}！`
          await new Promise((resolve) => {
            window.addEventListener('click', () => {
              resolve()
            })
          })
          this.mainMsg = `勝者：${who.name}！<br>再次點擊開始新賽局。`
          await new Promise((resolve) => {
            window.addEventListener('click', () => {
              resolve()
            })
          })
          this.init()
        }
      }
      who.position += 1 // 人物前進了一格
    },
    async event() {
      let e = Math.floor(Math.random() * 8)
      switch (e) {
        case 0:
          this.mainMsg = '順風吹拂，<br>額外前進三格。'
          await new Promise((resolve) => {
            window.addEventListener('click', () => {
              resolve()
            })
          })
          this.move(this.hato)
          this.move(this.hato)
          this.move(this.hato)
          break
        case 1:
          this.mainMsg = '發現美味蚱蜢！<br>小鴿子飽餐一頓。'
          await new Promise((resolve) => {
            window.addEventListener('click', () => {
              resolve()
            })
          })
          this.mainMsg = '行動值增加2點。'
          await new Promise((resolve) => {
            window.addEventListener('click', () => {
              resolve()
            })
          })
          this.hato.actions += 2
          break
        case 2:
          this.mainMsg = '發現了幸運草，<br>或許將有好事發生？'
          await new Promise((resolve) => {
            window.addEventListener('click', () => {
              resolve()
            })
          })
          this.superDice = true
          break
        case 3:
          this.mainMsg = '遭遇逆風，<br>額外消耗了體力。'
          await new Promise((resolve) => {
            window.addEventListener('click', () => {
              resolve()
            })
          })
          this.mainMsg = '行動值降低1點。'
          await new Promise((resolve) => {
            window.addEventListener('click', () => {
              resolve()
            })
          })
          if (this.hato.actions > 0) this.hato.actions--
          break
        case 4:
          this.mainMsg = '踩到了鳥屎，<br>感覺不太吉利……'
          await new Promise((resolve) => {
            window.addEventListener('click', () => {
              resolve()
            })
          })
          this.shitDice = true
          break
        case 5:
          this.mainMsg = '發現了蚯蚓，<br>小鴿子飽餐一頓。'
          await new Promise((resolve) => {
            window.addEventListener('click', () => {
              resolve()
            })
          })
          this.mainMsg = '行動值增加1點。'
          await new Promise((resolve) => {
            window.addEventListener('click', () => {
              resolve()
            })
          })
          this.hato.actions += 1
          break
        case 6:
          this.mainMsg = '發現了雜草，<br>小鴿子飽餐一頓。'
          await new Promise((resolve) => {
            window.addEventListener('click', () => {
              resolve()
            })
          })
          this.mainMsg = '行動值增加1點。'
          await new Promise((resolve) => {
            window.addEventListener('click', () => {
              resolve()
            })
          })
          this.hato.actions += 1
          break
        case 7:
          this.mainMsg = '發現了露水，<br>小鴿子飽餐一頓。'
          await new Promise((resolve) => {
            window.addEventListener('click', () => {
              resolve()
            })
          })
          this.mainMsg = '行動值增加1點。'
          await new Promise((resolve) => {
            window.addEventListener('click', () => {
              resolve()
            })
          })
          this.hato.actions += 1
          break
      }
      this.mainMsg = ''
    }
  },
  computed: {
    hatoReverse() {
      return this.hato.position >= 13 ? ';transform:scaleX(-1)' : '' // 地圖過半後改變面向左
    },
    redBirdReverse() {
      return this.redBird.position >= 13 ? ';transform:scaleX(-1)' : ''
    }
  },
  mounted() {
    this.init()
    setInterval(() => {
      // 輪播圖切換角色圖片
      this.hato.urlSwitch = !this.hato.urlSwitch
      this.redBird.urlSwitch = !this.redBird.urlSwitch
    }, 500)
  }
}
</script>

<style lang="less" scoped>
img {
  // 所有角色圖片填滿figure標籤
  width: 100%;
  height: 100%;
}
// 遊戲螢幕框
.container {
  position: relative;
  margin: 0 auto;
  padding: 4vh 3vw;
  width: 90vw;
  background: url('../assets/sky.png') no-repeat;
  background-size: cover;
  .score {
    position: absolute;
    font-size: 1.5rem;
    @media (min-width: 992px) {
      font-size: 3.5rem;
    }
  }
  // 遊戲角色
  .birds {
    position: absolute;
    z-index: 10;
    transition: 1s;
    width: 9vw;
    height: 15vh;
  }
  // 骰子
  .dice {
    position: absolute;
    right: 2vw;
    bottom: 20vh;
  }
  // 右下角行動力
  .ui {
    display: flex;
    position: absolute;
    right: 4vw;
    bottom: 2vh;
    figure {
      margin-right: 2vw;
      width: 6vw;
      height: 15vh;
    }
    p {
      margin-top: 1rem;
      font-size: 1.5rem;
      @media (min-width: 992px) {
        margin-top: 5vh;
        font-size: 2rem;
      }
    }
  }
  // 畫面中央提示框
  .hint {
    position: absolute;
    left: 55%;
    top: 15%;
    background-color: rgba(251, 52, 2, 0.191);
    font: italic 700 2rem Comic Sans MS;
    @media (min-width: 992px) {
      font-size: 3.5rem;
    }
  }
  .mainMsg {
    position: absolute;
    left: 35%;
    top: 38%;
    @media (min-width: 992px) {
      left: 33%;
      font-size: 2.5rem;
    }
  }
  // 賽道右側雲格
  .floatRight {
    float: right;
  }
  // 雲格
  figure {
    display: inline-block;
    margin: 0;
    width: 9vw;
    height: 15vh;
  }
  // 第n行雲格
  .first {
    padding: 0 0 0 36vw;
  }
  .second {
    padding: 0 0 0 18vw;
  }
  .fourth {
    padding: 0 9vw 0 0;
  }
  .fifth {
    padding: 0 18vw 0 0;
  }
}
</style>
